"use client";

import React, { useState, useEffect, use, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { toast } from 'react-hot-toast';
import { Venda, vendaService, VendaFinal, vendaFinalService } from '@/services/api/venda.service';
import VendaFinalCard from '@/components/vendas/VendaFinalCard';
import { Modal } from '@/components/ui/modal';
import VendaFinalForm from '@/components/vendas/VendaFinalForm';
import Button from '@/components/ui/Button';
import Input from '@/components/form/input/InputField';
import Pagination from '@/components/ui/Pagination';
import Label from '@/components/form/Label';
import { formatCurrency, formatDate } from '@/utils/format';
import { ChevronLeftIcon } from '@/icons';

export default function VendasFinaisPage({ params }: { params: Promise<{ vendaId: string }> }) {
  const { vendaId } = use(params);
  const router = useRouter();
  
  const [venda, setVenda] = useState<Venda | null>(null);
  const [vendasFinais, setVendasFinais] = useState<VendaFinal[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedVendaFinal, setSelectedVendaFinal] = useState<VendaFinal | null>(null);
  const [itemsPerPage, setItemsPerPage] = useState(9);

  const loadVenda = useCallback(async () => {
    try {
      const response = await vendaService.getById(Number(vendaId));
      setVenda(response.data);
    } catch (error) {
      toast.error('Erro ao carregar venda');
    }
  }, [vendaId]);

  const loadVendasFinais = useCallback(async () => {
    try {
      setLoading(true);
      const response = await vendaFinalService.list({ 
        page, 
        limit: itemsPerPage, 
        search: searchTerm,
        vendaId: Number(vendaId)
      });
      
      // Filtrar apenas as vendas-finais desta venda específica
      // const filteredVendasFinais = response.data.filter(vf => vf.vendaId === Number(vendaId));
      setVendasFinais(response.data);
      
      // Calcular páginas baseado nos dados filtrados
      setTotalPages(response.pagination.pages);
    } catch (error) {
      toast.error('Erro ao carregar faturas');
    } finally {
      setLoading(false);
    }
  }, [page, searchTerm, itemsPerPage, vendaId]);

  useEffect(() => {
    loadVenda();
  }, [loadVenda]);

  useEffect(() => {
    loadVendasFinais();
  }, [loadVendasFinais]);

  const handleEdit = (vendaFinal: VendaFinal) => {
    setSelectedVendaFinal(vendaFinal);
    setIsModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir esta fatura?')) return;

    try {
      await vendaFinalService.delete(id);
      toast.success('Fatura excluída com sucesso');
      loadVendasFinais();
    } catch (error) {
      toast.error('Erro ao excluir fatura');
    }
  };

  const handleSave = async (data: any) => {
    try {
      if (selectedVendaFinal) {
        await vendaFinalService.update(selectedVendaFinal.id, data);
        toast.success('Fatura atualizada com sucesso');
      } else {
        await vendaFinalService.create(data);
        toast.success('Fatura criada com sucesso');
      }
      setIsModalOpen(false);
      setSelectedVendaFinal(null);
      loadVendasFinais();
    } catch (error) {
      toast.error('Erro ao salvar fatura');
    }
  };

  const handleSend = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja enviar esta fatura para o cliente?')) return;

    try {
      await vendaFinalService.send(id);
      toast.success('Fatura enviada com sucesso para o cliente');
    } catch (error) {
      toast.error('Erro ao enviar fatura');
    }
  };

  const goBack = () => {
    router.push('/dashboard/empresa/financeiro/vendas');
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      {/* Header com informações da venda */}
      <div className="mb-8">
        <div className="mb-2">
            <Button
              variant="secondary"
              size="xs"
              onClick={goBack}
              className="mb-2 bg-transparent"
            >
              <ChevronLeftIcon className="w-5 h-5" />
              Voltar
            </Button>
          {/* <Button
            variant="secondary"
            onClick={goBack}
            className="p-2"
          >
            <svg className="w-5 h-5" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M9.707 16.707a1 1 0 01-1.414 0l-6-6a1 1 0 010-1.414l6-6a1 1 0 011.414 1.414L5.414 9H17a1 1 0 110 2H5.414l4.293 4.293a1 1 0 010 1.414z" clipRule="evenodd" />
            </svg>
          </Button> */}
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Faturas da Venda #{vendaId}
            </h1>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              Gerencie as faturas e pagamentos desta venda
            </p>
          </div>
        </div>

        {/* Informações da venda */}
        {venda && (
          <div className="bg-gray-50 dark:bg-gray-800 rounded-lg p-6 mb-6">
            <h2 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
              Informações da Venda
            </h2>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
              <div>
                <dt className="text-sm font-medium text-gray-500 dark:text-gray-400">Valor Total</dt>
                <dd className="text-lg font-semibold text-green-600 dark:text-green-400">
                  {formatCurrency(venda.valorTotal)}
                </dd>
              </div>
              <div>
                <dt className="text-sm font-medium text-gray-500 dark:text-gray-400">Status</dt>
                <dd className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                  venda.status === 'PAGO' 
                    ? 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-400'
                    : 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/20 dark:text-yellow-400'
                }`}>
                  {venda.status === 'PAGO' ? 'Pago' : 'Pendente'}
                </dd>
              </div>
              <div>
                <dt className="text-sm font-medium text-gray-500 dark:text-gray-400">Início</dt>
                <dd className="text-sm text-gray-900 dark:text-white">
                  {formatDate(venda.inicio)}
                </dd>
              </div>
              <div>
                <dt className="text-sm font-medium text-gray-500 dark:text-gray-400">Fim</dt>
                <dd className="text-sm text-gray-900 dark:text-white">
                  {formatDate(venda.fim)}
                </dd>
              </div>
            </div>
          </div>
        )}

        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div className="flex flex-col sm:flex-row gap-4 items-center">
            <Input
              placeholder="Buscar faturas..."
              value={searchTerm}
              onChange={(e) => {
                setSearchTerm(e.target.value);
                setPage(1);
              }}
              className="max-w-xs"
            />
          </div>
          <Button
            variant="primary"
            onClick={() => {
              setSelectedVendaFinal(null);
              setIsModalOpen(true);
            }}
          >
            Nova Fatura
          </Button>
        </div>

        <div className="mt-6">
          <div className="flex justify-between mb-4 gap-2 flex-wrap">
            <div className='flex items-center gap-0.5'>
              <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
              <Input
                className='max-w-24'
                defaultValue={itemsPerPage}
                max={100}
                min={1}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  const newValue = Number(e.target.value) || 9;
                  setItemsPerPage(newValue);
                  setPage(1);
                }}
              />
            </div>
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      </div>

      {/* Lista de faturas */}
      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-64" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {vendasFinais.length > 0 ? (
            vendasFinais.map((vendaFinal) => (
              <VendaFinalCard
                key={vendaFinal.id}
                vendaFinal={vendaFinal}
                onEdit={() => handleEdit(vendaFinal)}
                onDelete={() => handleDelete(vendaFinal.id)}
                onSend={() => handleSend(vendaFinal.id)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhuma fatura encontrada para esta venda.
              </p>
            </div>
          )}
        </div>
      )}

      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      )}

      {/* Modal para criar/editar fatura */}
      <Modal
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedVendaFinal(null);
        }}
        className="max-w-[800px] m-4"
      >
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedVendaFinal ? 'Editar Fatura' : 'Nova Fatura'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedVendaFinal 
                ? 'Atualize as informações da fatura.' 
                : 'Preencha os dados para criar uma nova fatura.'
              }
            </p>
          </div>
          <VendaFinalForm
            vendaFinal={selectedVendaFinal}
            vendaId={Number(vendaId)}
            onSubmit={handleSave}
            onCancel={() => {
              setIsModalOpen(false);
              setSelectedVendaFinal(null);
            }}
          />
        </div>
      </Modal>
    </div>
  );
}
